import styled from 'styled-components';
import { lighten, darken } from 'polished';
import { View } from './View';

export const Button = styled(View).attrs((props) => ({
  as: 'button',
  className: ['btn', props.className].filter((v) => v !== '').join(' '),
}))``;

export const PrimaryButton = styled(Button).attrs((props) => ({
  className: ['btn-primary', props.disabled ? 'disabled' : '', props.className]
    .filter((v) => v !== '')
    .join(' '),
}))`
  border-color: ${(props) => props.theme.light.colors.primary};
  background-color: ${(props) => props.theme.light.colors.primary};
  &:hover {
    border-color: ${(props) => darken(0.05, props.theme.light.colors.primary)};
    background-color: ${(props) => darken(0.05, props.theme.light.colors.primary)};
  }
  &.disabled {
    border-color: ${(props) => lighten(0.05, props.theme.light.colors.primary)};
    background-color: ${(props) => lighten(0.05, props.theme.light.colors.primary)};
  }
  &:not(:disabled):not(.disabled):active {
    border-color: ${(props) => darken(0.1, props.theme.light.colors.primary)};
    background-color: ${(props) => darken(0.1, props.theme.light.colors.primary)};
  }

  @media (prefers-color-scheme: dark) {
    border-color: ${(props) => props.theme.dark.colors.primary};
    background-color: ${(props) => props.theme.dark.colors.primary};
    &:hover {
      border-color: ${(props) => darken(0.05, props.theme.dark.colors.primary)};
      background-color: ${(props) => darken(0.05, props.theme.dark.colors.primary)};
    }
    &.disabled {
      border-color: ${(props) => lighten(0.05, props.theme.dark.colors.primary)};
      background-color: ${(props) => lighten(0.05, props.theme.dark.colors.primary)};
    }
    &:not(:disabled):not(.disabled):active {
      border-color: ${(props) => darken(0.1, props.theme.dark.colors.primary)};
      background-color: ${(props) => darken(0.1, props.theme.dark.colors.primary)};
    }
  }
`;

export const Link = styled(View).attrs((props) => ({
  className: ['btn-link', props.className].filter((v) => v !== '').join(' '),
}))`
  cursor: pointer;
  display: inline;
  padding: 0;
  vertical-align: baseline;
  color: ${(props) => props.theme.light.colors.primary} !important;

  @media (prefers-color-scheme: dark) {
    color: ${(props) => props.theme.dark.colors.primary} !important;
  }
`;
